body h1 {
  margin-top: 0;
  color: #fff;
  font-size: 60px;
  text-align: center;
}

.spooterfi-app {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

body button {
  font-size: 32px;
  padding: 8px 32px;
  border-radius: 0;
  border-style: dotted;
  /* background: #D61727; */
  background: #6efdf2;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-bottom: 20px;
}

body button {
  outline-color: #fff;
}

body input:focus-visible,
body select:focus-visible {
  outline-color: #b60ddc;
}

body button:not(:disabled):hover {
  text-decoration: underline;
  filter: brightness(1.2);
}

body button:disabled {
    cursor: no-drop;
}

input {
  font-size: 20px;
  font-family: 'Merchant Copy';
  padding: 10px 20px;
